  .navbar__hamburgerMenu__navbar {
    list-style: none;
    position: relative;
    bottom: 40px;
    overflow: hidden;
    max-height: 0;
    transition: 0.5s ease-out;
    width: 100%;
    height: 176px;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
    background-color: white;
    z-index: 2;
  }
  .navbar__hamburgerMenu__navbar__menu {
    height: 70%;
  }
  .navbar__hamburgerMenu__navbar__menu a {
    text-decoration: none;
    display: block;
    position: relative;
    left: 30px;
    font-size: 15px;
    font-family: Open Sans;
    text-transform: uppercase;
    letter-spacing: 3.28px;
    text-align: left;
    line-height: 30px;
    top: 10px;
    color: #A5A5A5;
  }
  .navbar__hamburgerMenu__navbar__menu a:active {
    color: #000000;
  }
  .navbar__hamburgerMenu .hamb {
    cursor: pointer;
    float: right;
    padding: 40px 20px;
  }
  .navbar__hamburgerMenu .hamb-line {
    background-color: #07ACE6;
    display: block;
    height: 3px;
    position: relative;
    width: 30px;
    bottom: 40px;
  }
  .navbar__hamburgerMenu .hamb-line::before,
  .navbar__hamburgerMenu .hamb-line::after {
    background-color: #07ACE6;
    content: "";
    display: block;
    height: 3px;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 30px;
  }
  .navbar__hamburgerMenu .hamb-line::before {
    right: 0px;
    bottom: 5px;
  }
  .navbar__hamburgerMenu .hamb-line::after {
    top: 5px;
    right: 0px;
  }
  .navbar__hamburgerMenu .side-menu {
    display: none;
  }
  .navbar__hamburgerMenu .side-menu:checked ~ .hamb .hamb-line {
    background: transparent;
  }
  .navbar__hamburgerMenu .side-menu:checked ~ .navbar__hamburgerMenu__navbar {
    max-height: 100%;
    transition: 3s;
  }
  .navbar__hamburgerMenu .side-menu:checked ~ .hamb .hamb-line::before {
    transform: rotate(-45deg);
    top: 0;
  }
  .navbar__hamburgerMenu .side-menu:checked ~ .hamb .hamb-line::after {
    transform: rotate(45deg);
    top: 0;
  }
